</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">air-data-block</span> <span class="hljs-attr">:method</span>=<span class="hljs-string">"load"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height: 100px"</span>&gt;</span>
