<script>
  export default {
    data () {
      return {
        data: []
      }
    },
    methods: {
      load (cb) {
        // 
        setTimeout(() => {
          this.data = [1,2,3,4,5]
          const n = parseInt(Math.random() * 10)
          n < 3 && cb(0)
          n >= 3 && n < 6 && cb(1)
          n >= 6 && cb(-1)
        }, 1500)
      }
    }
  }
</script>


