<script>
  import addDays from 'date-fns/esm/addDays'
  import format from 'date-fns/esm/format'
  export default {
    data () {
      return {
        data: this.initData(),
        data2: this.initData()
      }
    },
    methods: {
      initData () {
        return Array.apply(null, {length: 365}).map((item, i) => ({
          date: format(addDays(new Date(), i), 'YYYY-MM-DD'),
          text: parseInt(Math.random() * 1000)
        }))
      }
    }
  }
</script>


